.solarSystem {
    transform-style: preserve-3d;
    pointer-events: none;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
}

.solarPlanet {
    position: relative;
    transform-style: preserve-3d;
    width: 100%;
    height: 100%;
}

.planetRadio {
    display: none;
}

.readRadio {
    display: none;
}

.panel {
    position: absolute;
    right: -520px;
    width: 300px;
    top: 0;
    height: 100vh;
    transition: all 0.2s;
    color: black;
    background: white;
    padding: 10px 60px 0px 60px;
    overflow: scroll;
}